import React, {Component, useEffect, useState} from 'react';
import 'bootstrap/dist/css/bootstrap.min.css'
import axios from "axios";
import LoadingUsersData from "./Components/LoadingUsersData";
import UserData from "./Components/UserData";
import OnLoadingUserData from "./Components/LoadingUsersData";


function App() {

    const DataLoading = OnLoadingUserData(UserData);

    const [appUsers, setappUsers] = useState(
        {
            loading: false,
            users: null,
        }
    )

    useEffect(() => {
        const apiURL = 'http://localhost:8000/api/users/'
        axios.get(apiURL).then((response) => {
            const allData = response.data;
            setappUsers(allData);
                });
            // method: "GET",
            // url: `http://localhost:8000/api/users/`
            // .then(response => response.data)
            // .then(data =>.se
    }, [setappUsers]);

    return (
        <div className="App">
            <DataLoading isLoading={appUsers.loading} persons={appUsers.users} />
        </div>
    );
}

export default App;
