function UserData(props) {
    const {users} = props

    if (!users || users.length === 0) return <p>Not Found Users</p>

    return (
        <div>
            <table>
                <thead>
                <tr>
                    <th>ID</th>
                    <th>Username</th>
                    <th>Email</th>
                    <th>Created</th>
                    <th>Group</th>
                </tr>
                </thead>
                <tbody>
                {
                    users.map((user) =>
                        <tr key={user.id}>
                            <td>{user.id}</td>
                            <td>{user.username}</td>
                            <td>{user.email}</td>
                            <td>{user.created}</td>
                            <td>{user.group}</td>
                        </tr>
                    )
                }
                </tbody>
            </table>
        </div>
    )
}

export default UserData;